import * as actions from '../actions'

const initialState = {}

const auth = (state = initialState, action) => {
    switch (action.type) {
        case actions.UsersActions.SET_USER:
            return Object.assign({}, {
                ...state,
                [action.user.login]: action.user,
            })
        default:
            return Object.assign({}, state)
    }
}

export default auth
