import BigNumber from 'bignumber.js'
import moment from 'moment'

import forms from '../constants/forms'

const { graphFormat, serverFormat } = forms

const dateField = ''

const getMyValues = (market, valueField, identifierInterestVal, interestVal) => {
    if (!market) {
        return []
    }
    const dataObj = {}

    market.forEach((item) => {
        dataObj[item[dateField]] = [].concat(dataObj[item[dateField]] || [], item)
    })

    return {
        keys: Object.keys(dataObj),
        values: Object.values(dataObj).map(i =>
            i.reduce((acc, item) => {
                let modifyedStr = item[valueField]
                const condition = interestVal === '*'
                    ? true
                    : item[identifierInterestVal] === interestVal

                if (modifyedStr.indexOf('.') !== modifyedStr.lastIndexOf('.')) {
                    modifyedStr = modifyedStr.slice(0, modifyedStr.lastIndexOf('.') - 1)
                        + modifyedStr.slice(modifyedStr.lastIndexOf('.') + 1)
                }
                return condition
                    ? typeof modifyedStr === 'string'
                        ? BigNumber(acc).plus(BigNumber(modifyedStr ? modifyedStr.replace(/\s/g, '') : 0)).toString()
                        : acc
                    : acc
            }, 0)),
    }
}

const getAxisX = (ArrOfInfoObjects) => {
    const resultArr = []
    if (Array.isArray(ArrOfInfoObjects) && Array.isArray(ArrOfInfoObjects[0].keys)) {
        ArrOfInfoObjects.forEach((i) => {
            i.keys.forEach((item) => {
                if (!resultArr.includes(item)) {
                    resultArr.push(item)
                }
            })
        })
    }
    return resultArr.map(i => moment(i, serverFormat).format(graphFormat))
}

const getAxisY = (oX, ArrOfInfoObjects) => {
    const resultArr = []
    if (Array.isArray(ArrOfInfoObjects) && Array.isArray(ArrOfInfoObjects[0].keys) && oX.length) {
        oX.forEach((date) => {
            let summ = '0'
            ArrOfInfoObjects.forEach((i) => {
                i.keys.forEach((item, index) => {
                    if (item === moment(date, graphFormat).format(serverFormat)) {
                        summ = BigNumber(summ).plus(i.values[index]).toString()
                    }
                })
            })
            resultArr.push(BigNumber(summ).toNumber())
        })
    }
    return resultArr
}

function QuickSort({ A, B }) {
    if (A && B && A.length && B.length) {
        if (A.length === 0) return []
        const a = []
        const aB = []
        const b = []
        const bB = []
        const p = A[0]
        const pB = B[0]
        for (let i = 1; i < A.length; i++) {
            if (moment(A[i], graphFormat).isBefore(p, graphFormat)) {
                a[a.length] = A[i]
                aB[aB.length] = B[i]
            } else {
                b[b.length] = A[i]
                bB[bB.length] = B[i]
            }
        }
        return {
            A: QuickSort({ A: a, B: aB }).A.concat(p, QuickSort({ A: b, B: bB }).A),
            B: QuickSort({ A: a, B: aB }).B.concat(pB, QuickSort({ A: b, B: bB }).B),
        }
    }
    return {
        A: [],
        B: [],
    }
}

export default {
    getAxisX,
    getAxisY,
    getMyValues,
    QuickSort,
}
